.SubscribeSection{
    font-family: 'Roboto Condensed';
    min-width: 340px;
}

.BackgroundLineTop,
.BackgroundLineBottom{
    height: 12px;
    background-image: url("../../Assets/subs-line-bg.png");
    background-repeat: repeat-x;
}

.innerTopSubSec{
    height: 50%;
    display: flex;
}

.DivForTxt{
    width: 550px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.UpTxt{
    color: #5d7d94;
    margin: 0;
    font-size: 28px;
}

.BotTxt {
    color: rgba(254, 95, 50, 0.932);
    margin: 0;
    font-weight: 700;
    font-size: 41px;
}

.DivForSubscr{
    width: 571px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.InnerDivForTel{  
    display: flex;
    justify-content: space-evenly;
    width: 75%;
}

.InnerDivForVib{
    display: flex;
    justify-content: space-evenly;
    width: 75%;
}

.ViberIcon,
.TelegramIcon{
    width: 55px;
    height: 55px;
    border-radius: 7px;
}

.ParTxtTel,
.ParTxtVib{
    color: #5d7d94;
    font-weight: 700;
    font-size: 21px;
}

.TelegramBtn,
.ViberBtn{
    width: 240px;
    border-radius: 7px;
    border: none;
    color: white;
    height: 55px;
    font-size: 18px;
}

.TelegramBtn{
    background-color: rgb(19, 166, 215);
}

.ViberBtn{
    background-color: rgb(107, 20, 147);
}

.innerBotSubSec{
    width: 100%;
    height: 25%;
    display: flex;
    justify-content: center;
    margin-left: 15px;
}

.ImgMailIcon{
    width: 55px;
    height: 55px;
}

.MailIcon{
    width: 55px;
    height: 55px;
    border-radius: 11px;
}

.divForParTxt{   
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100px;
}

.ParTxtMail{
    color: #5d7d94;
    font-weight: 700;
    font-size: 21px;
}

.FirstInput,
.SecondInput{
    width: 240px;
    border-radius: 7px;
    border: none;
    color: white;
    height: 55px;
    font-size: 18px;    
    border: 1px solid gray;
}

.Form{
    width: 770px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.MailBtn{
    width: 240px;
    border-radius: 7px;
    border: none;
    color: white;
    height: 55px;
    font-size: 18px;
    background-color: rgb(239, 131, 22);
}

.Wrapper{
    height: 320px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.DivContainer{
    
    display: flex;
    justify-content: center;
    background-image: url(data:image/png;base64,);
}

@media (max-width: 1166px){
    .Wrapper{
        height: 650px;
        width: 70%;
        justify-content: center;
    }

    .innerTopSubSec{
        width: 615px;
        display: flex;
        align-self: center;
        flex-direction: column;
        height: 50%;
    }

    .innerBotSubSec{
        display: flex;
        flex-direction: column;
        align-self: center;
        width: 552px;
        height: 45%;
        margin-top: -68px;
    }

    .Form{
        height: 180px;
        width: 100%;
        display: flex;
        flex-direction: column;
    }

    .DivForTxt{
        width: 100%;
        height: 35%;
        display: flex;
        justify-content: flex-start;
    }

    .UpTxt,
    .BotTxt{
        margin-top: 0;
        padding-top: 0;
        line-height: 39px;
    }
    
    .DivForSubscr{
        width: 100%;
        height: 50%;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
    }

    .divForParTxt{   
        width: 30%;
        display: flex;
        justify-content: flex-start;
        margin-left: -20px;
        align-items: flex-end;
    }

    .InnerDivForTel{  
        display: flex;
        justify-content: space-evenly;
        width: 100%;
    }
    
    .InnerDivForVib{
        display: flex;
        justify-content: space-evenly;
        width: 100%;
    }

    .ImgMailIcon{
        position: relative;
    }

    .ParTxtMail{
        position: relative;
        bottom: 32px;
        left: 94px;
    }

    #EspDivForParTxt{
        height: 20px;
        position: relative;
        top: 15px;
    }

    .FirstInput,
    .SecondInput,
    .MailBtn{
        height: 45px;
        width: 97%;
    }
}

@media (max-width: 640px){
    .Wrapper{
        height: 650px;
        width: 70%;
        justify-content: center;
    }

    .innerTopSubSec{
        width: 323px;
        display: flex;
        align-self: center;
        flex-direction: column;
        height: 50%;
    }

    .innerBotSubSec{
        display: flex;
        flex-direction: column;
        align-self: center;
        width: 340px;
        height: 45%;
        margin-top: -68px;
    }

    .UpTxt{
        font-size: 25px;
        
    }

    .BotTxt{
        font-size: 38px;
        line-height: 30px;
    }

    .TelegramIcon,
    .ViberIcon,
    .MailIcon{  
        width: 45px;
        height: 45px;
    }

    .TelegramBtn,
    .ViberBtn{
        height: 45px;
        width: 150px;
    }

    .ParTxtTel,
    .ParTxtVib,
    .ParTxtMail{
        font-size: 18px;
    }

    .ParTxtTel,
    .ParTxtVib{
        margin-top: 10px;
    }

    .divForParTxt{   
        width: 39%;
        margin-left: 10px;
    }

    .FirstInput,
    .SecondInput,
    .MailBtn{
        height: 45px;
        width: 95%;
    }

    #EspDivForParTxt{
        height: 20px;
        position: relative;
        top: 10px;
        right: 49px;
    }
}
